  let root; // æ ¹èŠ‚ç‚¹
  let currentParent; // ä¸‹ä¸€ä¸ªå­å…ƒç´ çš„çˆ¶å…ƒç´ 
  let stack = []; // æ ˆç»“æ„ï¼›æ ˆä¸­push/popå…ƒç´ èŠ‚ç‚¹ï¼Œå¯¹äºæ–‡æœ¬èŠ‚ç‚¹ï¼Œç›´æ¥pushåˆ°currentParent.childrenå³å¯ï¼Œä¸ç”¨pushåˆ°æ ˆä¸­
  // è¡¨ç¤ºå…ƒç´ å’Œæ–‡æœ¬çš„type
