      //åŒ¹é…åˆ°äº†å¼€å§‹æ ‡ç­¾ï¼Œå°±æŠŠ <tagname æˆªå–æ‰ï¼Œå¾€åç»§ç»­åŒ¹é…å±æ€§
      advance(start[0].length);

      // 2. å¼€å§‹é€’å½’åŒ¹é…æ ‡ç­¾å±æ€§
      // endä»£è¡¨ç»“æŸç¬¦å· > ï¼›å¦‚æœåŒ¹é…æˆåŠŸï¼Œæ ¼å¼ä¸ºï¼š['>', '']
      // attr è¡¨ç¤ºåŒ¹é…çš„å±æ€§
      let end, attr;
