        // attr[1]ä¸ºå±æ€§åï¼Œattr[3]/attr[4]/attr[5]ä¸ºå±æ€§å€¼ï¼Œå–å†³äºå±æ€§å®šä¹‰æ˜¯åŒå¼•å·/å•å¼•å·/æ— å¼•å·

        // åŒ¹é…æˆåŠŸä¸€ä¸ªå±æ€§ï¼Œå°±åœ¨templateä¸Šæˆªå–æ‰è¯¥å±æ€§ï¼Œç»§ç»­å¾€ååŒ¹é…
        advance(attr[0].length);
