// arrayMethodsæ˜¯ç»§æ‰¿è‡ªArray.prototypeï¼Œä¸ç›´æ¥ä¿®æ”¹Array.prototypeï¼Œä¸æ±¡æŸ“Array.prototype
let oldArrayPrototype = Array.prototype
export let arrayMethods = Object.create(oldArrayPrototype)

let methods = [
    'push',
    'pop',
    'unshift',
    'shift',
    'sort',
    'reverse',
    'splice'
]

methods.forEach(method => {
    arrayMethods[method] = function(...args) {
        const result = oldArrayPrototype[method].call(this,...args) // thisæŒ‡å‘è°ƒç”¨è¯¥æ–¹æ³•çš„dataï¼ˆå³ç»è¿‡å“åº”å¼å¤„ç†çš„æ•°ç»„ï¼‰

        // å¯¹äºæ•°ç»„ä¸­æ–°å¢çš„å…ƒç´ ï¼Œä¹Ÿéœ€è¦è¿›è¡Œç›‘æ§
        const ob = this.__ob__;
        let inserted;
        switch (method) {
            case 'push':
            case 'unshift':
                inserted = args;
                break;
            case "splice":
                inserted = args.slice(2);
            default:
                break;
        }
        // insertedæ˜¯ä¸ªæ•°ç»„ï¼Œéœ€è¦è°ƒç”¨observeArrayæ¥ç›‘æµ‹
        if (inserted) ob.observeArray(inserted);

        // æ•°ç»„æ´¾å‘æ›´æ–°
        ob.dep.notify()
        return result
    }
})