import { initState } from "./state";
import { compileToFunctions } from './compiler/index'
import { callHook, mountComponent } from './lifecycle'

export function initMixin(Vue) {
  Vue.prototype._init = function (options) {
    // thisæŒ‡å‘å®ä¾‹
    const vm = this;
    vm.$options = options; // åé¢ä¼šå¯¹optionsè¿›è¡Œæ‰©å±•
    callHook(vm, "beforeCreate");
    
    // åˆå§‹åŒ–çŠ¶æ€ï¼ŒåŒ…æ‹¬initPropsã€initMethodã€initDataã€initComputedã€initWatchç­‰
    initState(vm);

    callHook(vm, "created");
    // å¦‚æœæœ‰elå±æ€§ è¿›è¡Œæ¨¡æ¿æ¸²æŸ“
    if (vm.$options.el) {
      vm.$mount(vm.$options.el);
    }
  };

  Vue.prototype.$mount = function (el) {
    // $mount ç”±vueå®ä¾‹è°ƒç”¨ï¼Œæ‰€ä»¥thisæŒ‡å‘vueå®ä¾‹
    const vm = this;
    const options = vm.$options;
    el = document.querySelector(el);

    /**
     * 1. æŠŠæ¨¡æ¿è½¬åŒ–æˆrenderå‡½æ•°
     * 2. æ‰§è¡Œrenderå‡½æ•°ï¼Œç”ŸæˆVNode
     * 3. æ›´æ–°æ—¶è¿›è¡Œdiff
     * 4. äº§ç”ŸçœŸå®DOM
     */
    // å¯ä»¥ç›´æ¥åœ¨optionsä¸­å†™renderå‡½æ•°ï¼Œå®ƒçš„ä¼˜å…ˆçº§æ¯”templateé«˜
    if (!options.render) {
      let template = options.template;

      // å¦‚æœä¸å­˜åœ¨renderå’Œtemplateä½†æ˜¯å­˜åœ¨elå±æ€§ï¼Œåˆ™ç›´æ¥å°†templateèµ‹å€¼ä¸ºelå…ƒç´ 
      if (!template && el) {
        template = el.outerHTML;
      }

      // æœ€ç»ˆéœ€è¦æŠŠtempalteæ¨¡æ¿è½¬åŒ–æˆrenderå‡½æ•°
      if (template) {
        // å°†templateè½¬åŒ–æˆrenderå‡½æ•°
        const render = compileToFunctions(template);
        options.render = render;
      }
    }

    // è°ƒç”¨renderæ–¹æ³•ï¼Œæ¸²æŸ“æˆçœŸå®DOM
    // ç»„ä»¶æŒ‚è½½æ–¹æ³•
    return mountComponent(vm, el);
  };
}
