const defaultTagRE = /\{\{((?:.|\r?\n)+?)\}\}/g; // åŒ¹é…èŠ±æ‹¬å· {{  }}ï¼›æ•è·èŠ±æ‹¬å·é‡Œé¢çš„å†…å®¹
function gen(node) {
  // å¦‚æœæ˜¯å…ƒç´ ç±»å‹
  if (node.type == 1) {
    // ã€å…³é”®ã€‘é€’å½’åˆ›å»º
    return generate(node);
  } else {
    // elseå³æ–‡æœ¬ç±»å‹
    let text = node.text;

    // 1. å¦‚æœtextä¸­ä¸å­˜åœ¨èŠ±æ‹¬å·å˜é‡è¡¨è¾¾å¼
    if (!defaultTagRE.test(text)) {
      // _vè¡¨ç¤ºåˆ›å»ºæ–‡æœ¬
      return `_v(${JSON.stringify(text)})`;
    }

    // æ­£åˆ™æ˜¯å…¨å±€æ¨¡å¼ æ¯æ¬¡éœ€è¦é‡ç½®æ­£åˆ™çš„lastIndexå±æ€§ï¼Œä¸ç„¶ä¼šå¼•å‘åŒ¹é…bugï¼ˆdefaultTagRE.exec()åŒ¹é…å®Œä¸€æ¬¡åï¼Œå†æ¬¡åŒ¹é…ä¸ºnullï¼Œéœ€è¦é‡ç½®lastIndexï¼‰
    let lastIndex = (defaultTagRE.lastIndex = 0);
    let tokens = [];
    let match, index;

    // 2. å¦‚æœtextä¸­å­˜åœ¨èŠ±æ‹¬å·å˜é‡ï¼ˆä½¿ç”¨whileå¾ªç¯ï¼Œæ˜¯å› ä¸ºå¯èƒ½å­˜åœ¨å¤šä¸ª{{å˜é‡}}ï¼‰
    while ((match = defaultTagRE.exec(text))) {
      // matchå¦‚æœåŒ¹é…æˆåŠŸï¼Œå…¶ç»“æ„ä¸ºï¼š['{{myValue}}', 'myValue', index: indexof({) ]
      // indexä»£è¡¨åŒ¹é…åˆ°çš„ä½ç½®
      index = match.index;
      // åˆå§‹ lastIndex ä¸º0ï¼Œindex > lastIndex è¡¨ç¤ºåœ¨{{ å‰æœ‰æ™®é€šæ–‡æœ¬
      if (index > lastIndex) {
        // åœ¨tokensé‡Œé¢æ”¾å…¥ {{ ä¹‹å‰çš„æ™®é€šæ–‡æœ¬
        tokens.push(JSON.stringify(text.slice(lastIndex, index)));
      }
      // tokensä¸­æ”¾å…¥æ•è·åˆ°çš„å˜é‡å†…å®¹
      tokens.push(`_s(${match[1].trim()})`);
      // åŒ¹é…æŒ‡é’ˆåç§»ï¼Œç§»åˆ° }} åé¢
      lastIndex = index + match[0].length;
    }

    // 3. å¦‚æœåŒ¹é…å®Œäº†èŠ±æ‹¬å·ï¼Œtexté‡Œé¢è¿˜æœ‰å‰©ä½™çš„æ™®é€šæ–‡æœ¬ï¼Œé‚£ä¹ˆç»§ç»­push
    if (lastIndex < text.length) {
      tokens.push(JSON.stringify(text.slice(lastIndex)));
    }
    // _vè¡¨ç¤ºåˆ›å»ºæ–‡æœ¬
    return `_v(${tokens.join("+")})`;
  }
}

// ç”Ÿæˆå­èŠ‚ç‚¹ï¼šéå†childrenè°ƒç”¨gen(item)ï¼Œä½¿ç”¨é€—å·æ‹¼æ¥æ¯ä¸€é¡¹çš„ç»“æœ
function getChildren(el) {
  const children = el.children;
  if (children) {
    return `${children.map((c) => gen(c)).join(",")}`;
  }
}

// å¤„ç†attrs/propså±æ€§ï¼šå°†[{name: 'class', value: 'home'}, {name: 'style', value: "font-size:12px;color:red"}]
//                  è½¬åŒ–æˆ "class:"home",style:{"font-size":"12px","color":"red"}"
function genProps(attrs) {
  let str = "";
  for (let i = 0; i < attrs.length; i++) {
    let attr = attrs[i];
    // å¯¹attrså±æ€§é‡Œé¢çš„styleåšç‰¹æ®Šå¤„ç†
    if (attr.name === "style") {
      let obj = {};
      attr.value.split(";").forEach((item) => {
        let [key, value] = item.split(":");
        obj[key] = value;
      });
      attr.value = obj;
    }
    str += `${attr.name}:${JSON.stringify(attr.value)},`;
  }
  return `{${str.slice(0, -1)}}`;
}

// å°†astè½¬åŒ–æˆrenderå‡½æ•°
export function generate(ast) {
  let children = getChildren(ast);

  let code = `_c('${ast.tag}',${
    ast.attrs.length ? `${genProps(ast.attrs)}` : "undefined"
  }${children ? `,${children}` : ""})`;

  return code;
}
