export function patch(oldVnode, vnode, vm) {
  // å¦‚æœæ²¡æœ‰elï¼Œä¹Ÿæ²¡æœ‰oldVnode
  if (!oldVnode) {
    // ç»„ä»¶çš„åˆ›å»ºè¿‡ç¨‹æ˜¯æ²¡æœ‰elå±æ€§çš„
    return createElm(vnode);
  } else {
    // Vnodeæ²¡æœ‰è®¾ç½®nodeTypeï¼Œå€¼ä¸ºundefinedï¼›çœŸå®èŠ‚ç‚¹å¯ä»¥è·å–åˆ°nodeType
    const isRealElement = oldVnode.nodeType;

    // å¦‚æœæ˜¯åˆæ¬¡æ¸²æŸ“
    if (isRealElement) {
      const oldElm = oldVnode;
      const parentElm = oldElm.parentNode;
      // å°†è™šæ‹Ÿdomè½¬åŒ–æˆçœŸå®domèŠ‚ç‚¹
      const el = createElm(vnode);

      // æ’å…¥åˆ° è€çš„elèŠ‚ç‚¹ çš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹çš„å‰é¢ï¼Œå°±ç›¸å½“äºæ’å…¥åˆ°è€çš„elèŠ‚ç‚¹çš„åé¢
      // è¿™é‡Œä¸ç›´æ¥ä½¿ç”¨çˆ¶å…ƒç´ appendChildæ˜¯ä¸ºäº†ä¸ç ´åæ›¿æ¢çš„ä½ç½®
      parentElm.insertBefore(el, oldElm.nextSibling);

      // åˆ é™¤è€çš„elèŠ‚ç‚¹
      parentElm.removeChild(oldVnode);
      return el;
    } else {
        // å¦‚æœæ˜¯æ›´æ–°è§†å›¾
        const el = createElm(vnode);
        const oldVnode = vm.$el // vm.$elåœ¨åˆæ¬¡æ¸²æŸ“æ—¶èµ‹å€¼çš„
        const parentElm = oldVnode.parentNode;
        parentElm.insertBefore(el, oldVnode.nextSibling);
        parentElm.removeChild(oldVnode);
        // TODO....diffç®—æ³•
    }
  }
}

// è™šæ‹Ÿdomè½¬æˆçœŸå®dom
function createElm(vnode) {
  const { tag, data, key, children, text } = vnode;

  // åˆ¤æ–­è™šæ‹Ÿdom æ˜¯å…ƒç´ èŠ‚ç‚¹è¿˜æ˜¯æ–‡æœ¬èŠ‚ç‚¹ï¼ˆæ–‡æœ¬èŠ‚ç‚¹tagä¸ºundefinedï¼‰
  if (typeof tag === "string") {
    // è™šæ‹Ÿdomçš„elå±æ€§æŒ‡å‘çœŸå®domï¼Œæ–¹ä¾¿åç»­æ›´æ–°diffç®—æ³•æ“ä½œ
    vnode.el = document.createElement(tag);

    // è§£ævnodeå±æ€§
    updateProperties(vnode);

    // å¦‚æœæœ‰å­èŠ‚ç‚¹å°±é€’å½’æ’å…¥åˆ°çˆ¶èŠ‚ç‚¹é‡Œé¢
    children.forEach((child) => {
      return vnode.el.appendChild(createElm(child));
    });
  } else {
    vnode.el = document.createTextNode(text);
  }
  return vnode.el;
}

// è§£ævnodeçš„dataå±æ€§ï¼Œæ˜ å°„åˆ°çœŸå®domä¸Š
function updateProperties(vnode, oldProps = {}) {
  const newProps = vnode.data || {};
  const el = vnode.el; // çœŸå®èŠ‚ç‚¹

  // å¦‚æœæ–°çš„èŠ‚ç‚¹æ²¡æœ‰ éœ€è¦æŠŠè€çš„èŠ‚ç‚¹å±æ€§ç§»é™¤
  for (const k in oldProps) {
    if (!newProps[k]) {
      el.removeAttribute(k);
    }
  }

  // å¯¹styleæ ·å¼åšç‰¹æ®Šå¤„ç† å¦‚æœæ–°çš„æ²¡æœ‰ éœ€è¦æŠŠè€çš„styleå€¼ç½®ä¸ºç©º
  const newStyle = newProps.style || {};
  const oldStyle = oldProps.style || {};
  for (const key in oldStyle) {
    if (!newStyle[key]) {
      el.style[key] = "";
    }
  }

  // éå†æ–°çš„å±æ€§ è¿›è¡Œå¢åŠ æ“ä½œ
  for (const key in newProps) {
    if (key === "style") {
      for (const styleName in newProps.style) {
        el.style[styleName] = newProps.style[styleName];
      }
    } else if (key === "class") {
      el.className = newProps.class;
    } else {
      // ç»™è¿™ä¸ªå…ƒç´ æ·»åŠ å±æ€§ å€¼å°±æ˜¯å¯¹åº”çš„å€¼
      el.setAttribute(key, newProps[key]);
    }
  }
}
